html {
    scroll-behavior: smooth;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Manrope',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: var(--bg-primary);
    color: var(--text-color-primary-white);
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  a {
    text-decoration: none;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  
  *,
  ::before,
  ::after {
    box-sizing: border-box;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  button {
    cursor: pointer;
    font-family: inherit;
  }
  
  .container {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  
  body.modalIsOpen {
    overflow: hidden;
  }
  
  @media screen and (min-width: 320px) {
    .container {
      width: 320px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  
  @media screen and (min-width: 834px) {
    .container {
      width: 834px;
      padding-left: 27px;
      padding-right: 27px;
    }
  }
  
  @media screen and (min-width: 1440px) {
    .container {
      width: 1440px;
      padding-left: 128px;
      padding-right: 128px;
    }
  }